import { createClient } from '@supabase/supabase-js';

// 
// .env.local VITE_SUPABASE_URLVITE_SUPABASE_ANON_KEY
const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;

// 
const hasValidConfig = Boolean(supabaseUrl && supabaseAnonKey);

// 
if (!hasValidConfig) {
  console.error('Supabase');
  console.error('VITE_SUPABASE_URLVITE_SUPABASE_ANON_KEY.env');
}

// 
const clearStoredSession = () => {
  console.log(' ');
  try {
    // localStoragesupabase
    Object.keys(localStorage).forEach(key => {
      if (key.startsWith('sb-') || key.includes('supabase')) {
        localStorage.removeItem(key);
      }
    });
  } catch (error) {
    console.error(':', error);
  }
};

// Supabase
// 
const createMockClient = () => {
  console.log(' Supabase');
  
  return {
    auth: {
      getSession: async () => ({ data: { session: null }, error: null }),
      signOut: async () => {
        clearStoredSession();
        return { error: null };
      },
      signInWithOAuth: async () => ({ error: null }),
      onAuthStateChange: () => ({ 
        data: { subscription: { unsubscribe: () => {} } },
        error: null 
      })
    },
    from: () => ({
      select: () => ({ data: null, error: null }),
      insert: () => ({ data: null, error: null }),
      update: () => ({ data: null, error: null }),
      delete: () => ({ data: null, error: null }),
      eq: () => ({ data: null, error: null })
    })
  };
};

// Supabase
// GoTrueClient
let supabaseInstance;

export const supabase = (() => {
  if (!supabaseInstance) {
    if (hasValidConfig) {
      // 
      console.log(' Supabase');
      supabaseInstance = createClient(supabaseUrl, supabaseAnonKey, {
        auth: {
          persistSession: true,
          autoRefreshToken: true,
          detectSessionInUrl: true,
          // 
          storageKey: 'supabase-auth-token',
          flowType: 'pkce'  // 
        }
      });
    } else {
      // 
      supabaseInstance = createMockClient();
    }
  }
  return supabaseInstance;
})();

// 
export const clearSupabaseTokens = clearStoredSession; 