import { supabase } from '../supabase';

// 
export async function getUserSavingsGoal(userId: string): Promise<number | null> {
  try {
    // TypeScriptany
    const result = await (supabase
      .from('savings_goals')
      .select('target_amount') as any)
      .eq('user_id', userId)
      .single();

    const { data, error } = result;

    if (error) {
      console.error(':', error);
      return null;
    }

    return data?.target_amount || null;
  } catch (error) {
    console.error(':', error);
    return null;
  }
}

// 
export async function getUserSavedAmount(userId: string): Promise<number | null> {
  try {
    // TypeScriptany
    const result = await (supabase
      .from('savings_goals')
      .select('saved_amount') as any)
      .eq('user_id', userId)
      .single();

    const { data, error } = result;

    if (error) {
      console.error(':', error);
      return null;
    }

    return data?.saved_amount || null;
  } catch (error) {
    console.error(':', error);
    return null;
  }
}

// 
export async function setUserSavingsGoal(
  userId: string, 
  targetAmount: number
): Promise<boolean> {
  try {
    // 
    const { data: existingData } = await (supabase
      .from('savings_goals')
      .select('id') as any)
      .eq('user_id', userId)
      .single();

    if (existingData) {
      // 
      const { error } = await (supabase
        .from('savings_goals')
        .update({ target_amount: targetAmount }) as any)
        .eq('user_id', userId);

      if (error) {
        console.error(':', error);
        return false;
      }
    } else {
      // 
      const { error } = await supabase
        .from('savings_goals')
        .insert([{ user_id: userId, target_amount: targetAmount, saved_amount: 0 }]);

      if (error) {
        console.error(':', error);
        return false;
      }
    }

    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
}

// 
export async function updateUserSavedAmount(
  userId: string, 
  savedAmount: number
): Promise<boolean> {
  try {
    const { error } = await (supabase
      .from('savings_goals')
      .update({ saved_amount: savedAmount }) as any)
      .eq('user_id', userId);

    if (error) {
      console.error(':', error);
      return false;
    }

    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
}

// 
export async function resetUserSavings(userId: string): Promise<boolean> {
  try {
    const { error } = await (supabase
      .from('savings_goals')
      .update({ saved_amount: 0 }) as any)
      .eq('user_id', userId);

    if (error) {
      console.error(':', error);
      return false;
    }

    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
} 