import { useState, useEffect } from 'react'
import SavingsGoal from './components/SavingsGoal'
import SavingsDots from './components/SavingsDots'
import Auth from './components/Auth'
import { supabase, clearSupabaseTokens } from './supabase'
import {
  getUserSavingsGoal,
  getUserSavedAmount,
  setUserSavingsGoal,
  updateUserSavedAmount,
  resetUserSavings
} from './services/savingsService'

function App() {
  // 
  const [userId, setUserId] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [loadError, setLoadError] = useState<string | null>(null);
  
  // : 1000
  const [targetAmount, setTargetAmount] = useState<number>(10000000);
  
  // 
  const [currentAmount, setCurrentAmount] = useState<number>(0);
  
  // 
  const [needsRelogin, setNeedsRelogin] = useState(false);

  // Supabase
  useEffect(() => {
    console.log('=== Supabase ===');
    const hasUrl = Boolean(import.meta.env.VITE_SUPABASE_URL);
    const hasKey = Boolean(import.meta.env.VITE_SUPABASE_ANON_KEY);
    
    console.log('Supabase URL:', hasUrl ? '' : ' ');
    console.log('Supabase Key:', hasKey ? '' : ' ');
    
    if (!hasUrl || !hasKey) {
      console.error('.env');
      console.error('VITE_SUPABASE_URL=SupabaseURL');
      console.error('VITE_SUPABASE_ANON_KEY=Supabase');
      
      // 
      setLoadError('Supabase');
      setIsLoading(false); // 
    }
  }, []);

  // 
  useEffect(() => {
    const checkSession = async () => {
      console.log(' checkSession');
      setIsLoading(true);
      setLoadError(null);
      
      try {
        console.log(' ...');
        
        // 
        const timeoutPromise = new Promise((_, reject) => {
          setTimeout(() => {
            console.log(' ');
            reject(new Error(''));
          }, 10000);
        });
        
        // getSession
        console.log(' supabase.auth.getSession');
        const sessionPromise = supabase.auth.getSession();
        
        // Promise.race
        console.log(' Promise.race');
        const { data, error } = await Promise.race([
          sessionPromise,
          timeoutPromise.then(() => {
            console.log(' Promise');
            return { data: null, error: new Error('') };
          })
        ]);
        
        console.log(' Promise.race:', data ? '' : '');
        
        if (error) {
          console.log(' :', error.message);
          
          // 
          console.log(' ');
          clearSupabaseTokens();
          
          // 
          setNeedsRelogin(true);
          throw error;
        }
        
        if (data?.session?.user) {
          console.log(' :', data.session.user.id);
          setUserId(data.session.user.id);
          console.log(' loadUserData');
          try {
            await loadUserData(data.session.user.id);
            console.log(' loadUserData');
          } catch (userDataError) {
            console.error(' :', userDataError);
            // 
          }
        } else {
          console.log(' ');
          // 
          const savedAmount = localStorage.getItem('savings-amount');
          if (savedAmount) {
            setCurrentAmount(parseInt(savedAmount));
            console.log(' :', savedAmount);
          }
        }
        console.log(' try');
      } catch (error) {
        console.error(' :', error);
        if (error instanceof Error && (error.message.includes('') || error.message.includes('timeout'))) {
          setLoadError('');
        } else {
          setLoadError('');
        }
        
        // 
        console.log(' ');
        const savedAmount = localStorage.getItem('savings-amount');
        if (savedAmount) {
          setCurrentAmount(parseInt(savedAmount));
          console.log(' :', savedAmount);
        }
      } finally {
        console.log(' finally - ');
        // 
        setIsLoading(false);
        console.log(' :', false);
      }
    };
    
    console.log(' checkSession');
    checkSession();
    
    // 
    console.log(' ');
    const { data: authListener } = supabase.auth.onAuthStateChange(
      async (event, session) => {
        console.log(' :', event, session?.user?.id || '');
        
        if (event === 'SIGNED_IN' && session?.user) {
          console.log(' :', session.user.id);
          setUserId(session.user.id);
          setNeedsRelogin(false); // 
          await loadUserData(session.user.id);
        } else if (event === 'SIGNED_OUT') {
          console.log(' ');
          setUserId(null);
          setCurrentAmount(0);
          setTargetAmount(10000000);
          // 
          localStorage.removeItem('savings-amount');
          localStorage.removeItem('savings-dots');
        }
      }
    );
    
    return () => {
      console.log(' : ');
      authListener.subscription.unsubscribe();
    };
  }, []);
  
  // 
  const loadUserData = async (id: string) => {
    try {
      // 
      console.log('...');
      const goal = await getUserSavingsGoal(id);
      console.log(':', goal);
      if (goal !== null) {
        setTargetAmount(goal);
      } else {
        // 
        await setUserSavingsGoal(id, targetAmount);
      }
      
      // 
      const saved = await getUserSavedAmount(id);
      if (saved !== null) {
        setCurrentAmount(saved);
      }
    } catch (error) {
      console.error(':', error);
      setLoadError('');
    }
  };
  
  // 
  const handleLogin = async (id: string) => {
    console.log(':', id);
    setUserId(id);
    setNeedsRelogin(false); // 
    
    try {
      await loadUserData(id);
      console.log('');
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };
  
  // 
  const handleLogout = async () => {
    try {
      console.log('');
      const { error } = await supabase.auth.signOut();
      
      if (error) {
        throw error;
      }
      
      // 
      setUserId(null);
      setCurrentAmount(0);
      setTargetAmount(10000000); // 
      setLoadError(null); // 
      setNeedsRelogin(false); // 
      
      // 
      localStorage.removeItem('savings-amount');
      localStorage.removeItem('savings-dots');
      
      // 
      clearSupabaseTokens();
      
      // 
      window.location.reload();
      
      console.log('');
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };
  
  // 
  const handleRefreshSession = () => {
    // 
    clearSupabaseTokens();
    // 
    window.location.reload();
  };
  
  // 
  const handleTargetChange = async (newTarget: number) => {
    setTargetAmount(newTarget);
    
    if (userId) {
      await setUserSavingsGoal(userId, newTarget);
    }
  };
  
  // 
  const handleSavingsUpdate = async (amount: number) => {
    const newAmount = Math.max(0, currentAmount + amount);
    setCurrentAmount(newAmount);
    
    if (userId) {
      // Supabase
      await updateUserSavedAmount(userId, newAmount);
    } else {
      // 
      localStorage.setItem('savings-amount', newAmount.toString());
    }
  };

  // 
  const handleReset = async () => {
    if (window.confirm('')) {
      // 
      setCurrentAmount(0);
      
      if (userId) {
        // Supabase
        await resetUserSavings(userId);
      } else {
        // 
        localStorage.removeItem('savings-amount');
        localStorage.removeItem('savings-dots');
      }
    }
  };

  // 
  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <p className="text-lg">...</p>
      </div>
    );
  }
  
  // Auth
  if ((loadError && !userId) || needsRelogin) {
    return (
      <div className="flex flex-col min-h-screen bg-gray-50">
        <header className="bg-green-50 py-6 border-b-2 border-secondary">
          <div className="container mx-auto px-4 text-center">
            <h1 className="text-3xl font-bold text-primary mb-2"></h1>
            <p className="text-gray-600"></p>
          </div>
        </header>
        
        <main className="flex-1 container mx-auto px-4 py-6">
          <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-6">
            <p>{needsRelogin ? '' : loadError}</p>
            <p className="mt-2">
              {loadError?.includes('') 
                ? ''
                : ''
              }
            </p>
            
            {/*  */}
            {needsRelogin && (
              <div className="mt-3">
                <button 
                  onClick={handleRefreshSession}
                  className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded"
                >
                  
                </button>
              </div>
            )}
          </div>
          
          {loadError?.includes('') ? (
            // SavingsGoal
            <>
              <SavingsGoal 
                currentAmount={currentAmount} 
                targetAmount={targetAmount} 
                onTargetChange={handleTargetChange}
              />
              
              <section className="mt-12">
                <div className="flex flex-col items-center mb-6">
                  <h3 className="text-xl font-semibold text-primary mb-2">1 = 10,0001000</h3>
                  <div className="text-sm text-gray-500 italic mb-4 text-center">
                    <p>ON/OFF</p>                    <p className="mt-1">1010</p>
                  </div>
                  <div className="flex items-center justify-center gap-4 my-2">
                    <div className="flex gap-0.5">
                      {Array(10).fill(0).map((_, i) => (
                        <div key={i} className="w-2 h-2 rounded-full bg-secondary"></div>
                      ))}
                    </div>
                    <div className="text-gray-500">=</div>
                    <div className="w-6 h-6 rounded-full bg-green-500 border border-green-600"></div>
                    <div className="text-sm text-gray-600">10</div>
                  </div>
                </div>
                
                <SavingsDots onSavingsUpdate={handleSavingsUpdate} currentAmount={currentAmount} targetAmount={targetAmount} />
                
                {/*  */}
                <div className="flex justify-end mt-8">
                  <button 
                    className="bg-danger hover:bg-red-700 text-white px-4 py-2 rounded transition transform hover:-translate-y-0.5 active:translate-y-0"
                    onClick={handleReset}
                    title=""
                  >
                    
                  </button>
                </div>
              </section>
            </>
          ) : (
            <Auth onLogin={handleLogin} />
          )}
        </main>
        
        <footer className="bg-green-50 py-4 border-t border-gray-200 mt-auto">
          <div className="container mx-auto px-4 text-center text-gray-600">
            <p> {new Date().getFullYear()} </p>
          </div>
        </footer>
      </div>
    );
  }

  return (
    <div className="flex flex-col min-h-screen bg-gray-50">
      <header className="bg-green-50 py-6 border-b-2 border-secondary">
        <div className="container mx-auto px-4 text-center">
          <h1 className="text-3xl font-bold text-primary mb-2"></h1>
          <p className="text-gray-600"></p>
          
          {loadError && loadError.includes('') && (
            <div className="mt-2 bg-yellow-100 text-yellow-800 px-3 py-1 rounded inline-block">
              
            </div>
          )}
          
          {userId && (
            <div className="mt-2 flex justify-center">
              <button 
                onClick={handleLogout}
                className="text-sm bg-gray-200 hover:bg-gray-300 px-3 py-1 rounded"
              >
                
              </button>
            </div>
          )}
        </div>
      </header>
      
      <main className="flex-1 container mx-auto px-4 py-6">
        {userId ? (
          <>
            <SavingsGoal 
              currentAmount={currentAmount} 
              targetAmount={targetAmount} 
              onTargetChange={handleTargetChange}
            />
            
            <section className="mt-12">
              <div className="flex flex-col items-center mb-6">
                <h3 className="text-xl font-semibold text-primary mb-2">1 = 10,0001000</h3>
                <div className="text-sm text-gray-500 italic mb-4 text-center">
                  <p>ON/OFF</p>
                  <p className="mt-1">150500,000/</p>
                </div>
                <div className="flex items-center justify-center gap-4 my-2">
                  <div className="flex gap-0.5">
                    {Array(10).fill(0).map((_, i) => (
                      <div key={i} className="w-2 h-2 rounded-full bg-secondary"></div>
                    ))}
                  </div>
                  <div className="text-gray-500">=</div>
                  <div className="w-6 h-6 rounded-full bg-green-500 border border-green-600"></div>
                  <div className="text-sm text-gray-600">10</div>
                </div>
              </div>
              
              <SavingsDots onSavingsUpdate={handleSavingsUpdate} currentAmount={currentAmount} targetAmount={targetAmount} />
              
              {/*  */}
              <div className="flex justify-end mt-8">
                <button 
                  className="bg-danger hover:bg-red-700 text-white px-4 py-2 rounded transition transform hover:-translate-y-0.5 active:translate-y-0"
                  onClick={handleReset}
                  title=""
                >
                  
                </button>
              </div>
            </section>
          </>
        ) : (
          <Auth onLogin={handleLogin} />
        )}
      </main>
      
      <footer className="bg-green-50 py-4 border-t border-gray-200 mt-auto">
        <div className="container mx-auto px-4 text-center text-gray-600">
          <p> {new Date().getFullYear()} </p>
        </div>
      </footer>
    </div>
  );
}

export default App
